.NotFound{
	width:100vw;
	height:100vh;
	display:-ms-flexbox;
	display:flex;
	-ms-flex-align:center;
	    align-items:center;
  	-ms-flex-pack:center;
  	    justify-content:center;
  	-ms-flex-wrap: wrap;
  	    flex-wrap: wrap;
  	background-color:white;
}
.NotFound div{
	width:400px;
	background-color:white;
	-webkit-box-shadow:0 0 4px 1;
	        box-shadow:0 0 4px 1;
	-moz-box-shadow:0 0 4px 1;

}
.NotFound p{
	color:grey;
}
.NotFound h4{
	color:black;
}
@media only screen and (max-width:7in){
	.NotFound div{
		width:300px;
	}
}


