.Events{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	-moz-box-sizing: border-box;	
	height:100%;
	-webkit-transition:0.5s;
	-o-transition:0.5s;
	transition:0.5s;

}
#Events{
	position:relative;
	overflow:hidden;
	width:200vw;
}
#eventsCardCont{
	/*background-color: white;*/
	display:-ms-flexbox;
	display:flex;
	-ms-flex-align:center;
	    align-items:center;
  	-ms-flex-pack:distribute;
  	    justify-content:space-around;
  	-ms-flex-wrap: wrap;
  	    flex-wrap: wrap;
  	-ms-flex-direction:row;
  	    flex-direction:row;
  	overflow-y:auto;
  	width:100vw;
	position:absolute;
	top:0;
	left:00vw;
	padding-bottom:10vh !important;

}
#eventsCardDtls{
	position:absolute;
	top:0;
	left:100vw;
	padding-bottom: 10vh;
	overflow-y:auto;
}
@media only screen and (max-width:10in){
	#eventsCardCont{
		/*overflow-y:scroll;*/
	}
}
