
  
  .rocket {
    position: absolute;
    bottom: -600px;
    
    -webkit-animation: fly 3s .5s ease-in;
    
            animation: fly 3s .5s ease-in;
  }
  
  @-webkit-keyframes fly {
    0% {
      bottom: -600px;   
    }
    100% {
      bottom: 100%;
    }
  }
  
  @keyframes fly {
    0% {
      bottom: -600px;   
    }
    100% {
      bottom: 100%;
    }
  }
  
  


