.FooterMenu-wrapper{
	position:absolute;
	bottom:0%;
	left:0;
	width:100%;
	height:10%;
	display:none;
	-ms-flex-align:center;
	    align-items:center;
	-ms-flex-pack: center;
	    justify-content: center;
	background-color: rgba(0,0,0,1);
	display: none;
}
.fa {
  padding: 12px;
  font-size: 17px;
  width: 17px;
  text-align: center;
  text-decoration: none;
  margin: 5px 5px;

}


@media only screen and (max-width:8in){
	.fa{
	  padding: 7px;
	  font-size: 14px;
	  width: 14px;	
	}
	.FooterMenu-wrapper{
		display:-ms-flexbox;
		display:flex;
	}
}


