.Lectures{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-pack: center;
      justify-content: center;
  height:100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -moz-box-sizing: border-box;
  overflow: hidden;
  width:100vw;
  color:lightgrey;
  font-family:'Nova Flat', cursive;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.images{
    margin:0 2px;
	width: 33%;
  }
@media only screen and (max-width:8in){
  .images{
      width:97%;
      height:initial;
      margin:0;
      margin-top:20px;
  }
  .Lectures{
      height:initial;
      overflow:initial;
  }
}



